<template>
<example-defender :my-list="list">
  <template #defender>
    <p class="font-bold text-white font-black w-full">مدافعان تیم لیورپول</p>
  </template>
</example-defender>
</template>

<script>
import {onMounted, reactive} from "vue";
import axios from "axios";
import ExampleDefender from "@/components/main/ExampleDefender";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "DefenderLiver",
  components: {ExampleDefender},
  setup(){
    const list=reactive([])
    const result=onMounted(async ()=> {
     await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/78'),
         axios.get('https://api.footballi.net/api/v2/player/476'),
         axios.get('https://api.footballi.net/api/v2/player/1477'),
         axios.get('https://api.footballi.net/api/v2/player/12477'),
         axios.get('https://api.footballi.net/api/v2/player/2222546'),
         axios.get('https://api.footballi.net/api/v2/player/2257260'),
         axios.get('https://api.footballi.net/api/v2/player/2257325'),
         axios.get('https://api.footballi.net/api/v2/player/2258028'),
         axios.get('https://api.footballi.net/api/v2/player/2310050'),
         axios.get('https://api.footballi.net/api/v2/player/2333761'),
      ])
          .then(axios.spread((...response) => {
            list.push(response)
          }))
          .catch((error) => {
           handleError(error)
          })

    })
    return{
      list,result
    }
  }
}
</script>

