<template>
<example-super-goal :my-list="players">

</example-super-goal>
</template>

<script>
import {onMounted, reactive} from "vue";
import axios from "axios";
import ExampleSuperGoal from "@/components/main/ExampleSuperGoal";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "SuperGoalLiver",
  components: {ExampleSuperGoal},
  setup(){
    const players=reactive([])
    const total=onMounted(async ()=>{
      await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/3926'),
         axios.get('https://api.footballi.net/api/v2/player/2396583'),
         axios.get('https://api.footballi.net/api/v2/player/2237094'),
         axios.get('https://api.footballi.net/api/v2/player/2228658'),
      ])
          .then(axios.spread((...response)=>{
            players.push(response)
          }))
          .catch((error)=>{
           handleError(error)
          })

    })
    return{
      players,total
    }
  }
}
</script>

