<template>
<example-attack :my-list="list">
  <template #Midfielder>
    <p class="font-bold text-white font-black w-full">هافبک های تیم لیورپول</p>
  </template>
</example-attack>
</template>

<script>
import {onMounted, reactive} from "vue";
import axios from "axios";
import ExampleAttack from "@/components/main/ExampleAttack";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "AttackLiver",
  components: {ExampleAttack},
  setup(){
    const list=reactive([]);
    const total=onMounted(async ()=>{
      await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/194'),
         axios.get('https://api.footballi.net/api/v2/player/324'),
         axios.get('https://api.footballi.net/api/v2/player/674'),
         axios.get('https://api.footballi.net/api/v2/player/4740'),
         axios.get('https://api.footballi.net/api/v2/player/2225724'),
         axios.get('https://api.footballi.net/api/v2/player/2231171'),
         axios.get('https://api.footballi.net/api/v2/player/2257670'),
         axios.get('https://api.footballi.net/api/v2/player/2260373'),
         axios.get('https://api.footballi.net/api/v2/player/2342004'),
         axios.get('https://api.footballi.net/api/v2/player/2449679'),
      ])
          .then(axios.spread((...res)=>{
            list.push(res)
          }))
          .catch((error)=>{
          handleError(error)
          })
    })
    return{
      list,total
    }
  }
}
</script>

