<template>
<example-transfer :my-list="menu">

</example-transfer>
</template>

<script>
import {reactive,onMounted} from "vue";
import axios from "axios";
import ExampleTransfer from "@/components/main/ExampleTransfer";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "TransferTeamLiver",
  components: {ExampleTransfer},
  setup(){
    const menu=reactive([])
    const sum=onMounted(async ()=>{
     await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/2285314'),
         axios.get('https://api.footballi.net/api/v2/player/2229662'),
         axios.get('https://api.footballi.net/api/v2/player/2237094'),
         axios.get('https://api.footballi.net/api/v2/player/3926'),
         axios.get('https://api.footballi.net/api/v2/player/2280572'),
         axios.get('https://api.footballi.net/api/v2/player/2229531'),
         axios.get('https://api.footballi.net/api/v2/player/5538'),
         axios.get('https://api.footballi.net/api/v2/player/190'),
         axios.get('https://api.footballi.net/api/v2/player/2396583'),
         axios.get('https://api.footballi.net/api/v2/player/324'),
         axios.get('https://api.footballi.net/api/v2/player/2310050'),
         axios.get('https://api.footballi.net/api/v2/player/2230210'),
         axios.get('https://api.footballi.net/api/v2/player/2334056'),
         axios.get('https://api.footballi.net/api/v2/player/2225724'),
         axios.get('https://api.footballi.net/api/v2/player/2363309'),
      ])
          .then(axios.spread((...response)=>{
            menu.push(response)
          }))
          .catch((error)=>{
            handleError(error)
          })

    })

    return{
      menu,sum
    }
  }
}
</script>

