<template>
<section class="center-players">
  <div class="container mx-auto">
    <div class="w-full h-auto">
      <p class="font-semibold text-start text-black w-full">بازیکنان تیم لیورپول</p>
      <div class="bg-black h-0.5 w-full my-1"></div>
      <div class="bg-green-500 w-full min-h-full relative px-3">
        <example-goal-keeper :my-list="result">
          <template #goalKeeper>
            <p class="font-bold text-white font-black w-full">دروازه بان های تیم لیورپول</p>
          </template>
        </example-goal-keeper>
        <defender-liver></defender-liver>
        <attack-liver></attack-liver>
        <main-attack></main-attack>
        <img :src="require('../../../assets/image/squad-middle.png')" alt="" class="h-auto absolute mx-auto bottom-2.5 w-full left-0 right-0">
      </div>
    </div>
  </div>
</section>
</template>

<script>
import ExampleGoalKeeper from "@/components/main/ExampleGoalKeeper";
import {onMounted, reactive} from "vue";
import axios from "axios";
import DefenderLiver from "@/components/main/TeamLiver/DefenderLiver";
import AttackLiver from "@/components/main/TeamLiver/AttackLiver";
import MainAttack from "@/components/main/TeamLiver/MainAttack";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "PlayersLiver",
  components: {MainAttack, AttackLiver, DefenderLiver, ExampleGoalKeeper},
  setup(){
    const result=reactive([]);
    const total=onMounted(async ()=>{
      await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/164'),
         axios.get('https://api.footballi.net/api/v2/player/22121'),
         axios.get('https://api.footballi.net/api/v2/player/2258033'),
      ])
          .then(axios.spread((...response)=>{
            result.push(response)
          }))
          .catch((error)=>{
            handleError(error)
          })

    })
    return{
      result,total
    }
  }
}
</script>

